<template>
    <v-flex>
        <v-list disabled three-line>
            <v-list-item-group color="primary">
                <v-list-item
                        v-for="(qualite, index) in qualites"
                        :key="`qualite-${index}`"
                        inactive
                >
                    <v-list-item-content>
                        <v-list-item-title v-html="qualite.nom"></v-list-item-title>
                        <v-list-item-subtitle v-html="qualite.description"></v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
            </v-list-item-group>
        </v-list>
    </v-flex>
</template>

<script>
    import axios from "axios";

    export default {
        name: "qualite",
        data: function() {
            return {
                details_file: "files/details.json",
                qualites: null
            };
        },

        methods: {
            getQua: function () {
                const self = this;
                axios.get(this.details_file).then(function (result) {
                    self.qualites = result.data.qualites;
                });
            }
        },

        mounted: function() {
            this.getQua();
        }
    }
</script>

