<template>
    <v-container>

        <br>
        <div id="domain_div">
            <h2>Compétences du domaine {{ domaine }} : </h2>

            <br>

            <div
                    v-for="(competence, index) in competences"
                    :key="`competence-${index}`"
            >
                <h3> {{ competence.nom }} </h3>
                <v-progress-linear
                        :value="competence.value"
                        color="blue"
                        height="25"
                >
                    <template v-slot="{ value }">
                        <strong>{{ Math.ceil(value) }}%</strong>
                    </template>
                </v-progress-linear>
                <br>
            </div>
        </div>

        <br>
        <br>
        <div id="langue">
            <h2>Langues :</h2>
            <br>
            <div
                    v-for="(langue, index) in langues"
                    :key="`langue-${index}`"
            >
                <h3> {{ langue.nom }} </h3>
                <v-progress-linear
                        :value="langue.value_writing"
                        color="green"
                        height="15"
                >
                    <template v-slot="{ value }">
                        <strong> Ecriture : {{ Math.ceil(value) }}%</strong>
                    </template>
                </v-progress-linear>
                <br>
                <v-progress-linear
                        :value="langue.value_reading"
                        color="green"
                        height="15"
                >
                    <template v-slot="{ value }">
                        <strong> Lecture : {{ Math.ceil(value) }}%</strong>
                    </template>
                </v-progress-linear>
                <br>
                <v-progress-linear
                        :value="langue.value_speaking"
                        color="green"
                        height="15"
                >
                    <template v-slot="{ value }">
                        <strong> Oral : {{ Math.ceil(value) }}%</strong>
                    </template>
                </v-progress-linear>
                <br>
            </div>
        </div>

    </v-container>
</template>

<script>
    import axios from "axios";

    export default {
        name: "competences",
        data: function() {
            return {
                details_file: "files/details.json",
                domaine: null,
                competences: null,
                langues: null,
            };
        },

        methods: {
            getComp: function () {
                const self = this;
                axios.get(this.details_file).then(function (result) {
                    self.competences = result.data.competences;
                });
            },
            getLang: function () {
                const self = this;
                axios.get(this.details_file).then(function (result) {
                    self.langues = result.data.langues;
                });
            },
            getDom: function () {
                const self = this;
                axios.get(this.details_file).then(function (result) {
                    self.domaine = result.data.domaine;
                });
            }
        },

        mounted: function() {
            this.getComp();
            this.getLang();
            this.getDom();
        }
    }
</script>

