<template>
    <v-flex>
        <p>{{ name }}</p>
        <p>{{ age }}</p>
        <p>{{ email }}</p>
        <p>{{ phone }}</p>
    </v-flex>
</template>

<script>
    import axios from "axios";

    export default {
        name: "details",
        data: function() {
            return {
                details_file: "files/details.json",
                age: null,
                name: null,
                email: null,
                phone: null
            };
        },
        methods: {
            getValue: function () {
                const self = this;
                axios.get(this.details_file).then(function (result) {
                    self.name = result.data.name;
                    self.age = result.data.age;
                    self.email = result.data.email;
                    self.phone = result.data.phone;
                });
            }
        },
        mounted: function() {
            this.getValue();
        }
    }
</script>

