<template>
    <v-container>
        <v-timeline :dense="windowWidth < 960">
            <v-timeline-item
                    v-for="(item, index) in experiences"
                    :key="`item-${index}`"
                    large
            >
                <template v-slot:icon>
                    <v-avatar>
                        <img src="../files/photo.png">
                    </v-avatar>
                </template>
                <template slot="opposite">
                    <span>{{ item.date_deb }}</span>
                </template>
                <template v-if="windowWidth < 960">
                    <span>{{ item.date_deb }}</span>
                </template>
                <v-card class="elevation-2">
                    <v-card-title class="headline">{{ item.nom }}</v-card-title>
                    <v-card-text>
                        <div id="description_div" >
                            {{ item.description }}
                        </div>
                        <div id="missions_div">
                            <v-list disabled>
                                <v-subheader>Missions :</v-subheader>
                                <v-list-item-group color="primary">
                                    <v-list-item
                                            v-for="(mission, index) in item.missions"
                                            :key="`mission-${index}`">
                                        <v-list-item-content v-html="mission"></v-list-item-content>
                                    </v-list-item>
                                </v-list-item-group>
                            </v-list>
                        </div>
                        <div id="timeEnd_div">
                            <v-card-text>
                                Terminé le : {{ item.date_fin }}
                            </v-card-text>
                        </div>
                    </v-card-text>
                </v-card>
            </v-timeline-item>
        </v-timeline>
    </v-container>
</template>

<script>
    import axios from "axios";

    export default {
        name: "experiences",
        data: function() {
            return {
                details_file: "files/details.json",
                experiences: null,
                experiences_name: null,
                windowWidth: window.innerWidth,
            };
        },

        methods: {
            getExp: function () {
                const self = this;
                axios.get(this.details_file).then(function (result) {
                    self.experiences = result.data.experiences;
                    self.experiences_name = Object.keys(self.experiences);
                    console.log(self.experiences_name);
                });
            },
            getDescExp: function(id) {
                    var string = this.experiences[id].description;
                    console.log(string);
                    return [string];
            }
        },

        mounted: function() {
            this.getExp();

            window.onresize = () => {
                this.windowWidth = window.innerWidth
            }
        }
    }
</script>

