<template>
    <v-card>
        <v-timeline :dense="windowWidth < 960">
            <v-timeline-item
                    v-for="(item, index) in parcours"
                    :key="`item-${index}`"
                    large
            >
                <template v-slot:icon>
                    <v-avatar>
                        <img src="../files/photo.png">
                    </v-avatar>
                </template>
                <template slot="opposite">
                    <span>{{ item.date_deb }}</span>
                </template>
                <template v-if="windowWidth < 960">
                    <span>{{ item.date_deb }} - {{ item.date_fin }}</span>
                </template>
                <v-card class="elevation-2">
                    <v-card-title class="headline">{{ item.nom }}</v-card-title>
                    <v-card-text>
                        <div id="description_div" >
                            {{ item.description }}
                        </div>
                    </v-card-text>
                </v-card>
            </v-timeline-item>
        </v-timeline>
    </v-card>
</template>

<script>
    import axios from "axios";

    export default {
        name: "parcours",
        data: function() {
            return {
                details_file: "files/details.json",
                parcours: null,
                parcours_name: null,
                windowWidth: window.innerWidth,
            };
        },

        methods: {
            getExp: function () {
                const self = this;
                axios.get(this.details_file).then(function (result) {
                    self.parcours = result.data.parcours;
                    self.parcours_name = Object.keys(self.parcours);
                    console.log(self.parcours_name);
                });
            },
            getDescExp: function(id) {
                var string = this.parcours[id].description;
                console.log(string);
                return [string];
            }
        },

        mounted: function() {
            this.getExp();

            window.onresize = () => {
                this.windowWidth = window.innerWidth
            }
        }
    }
</script>

