<template>
    <v-parallax
            dark
            src="./files/797.jpg"
            style="height: 100vh; width: 100vW"
    >
        <v-row>
            <template v-if="windowWidth < 960 && windowWidth >601">
                <v-col v-if="presentation" class="container-sm text-center" cols="5">
                    <div class="circle-sm"></div>
                    <h1 class="display-1 font-weight-thin mb-4">{{ presentation.titre }}</h1>
                    <h4 class="subheading">{{ presentation.description }}</h4>
                </v-col>
            </template>

            <template v-if="windowWidth >= 960">
                <v-col v-if="presentation" class="container text-center" cols="5">
                    <div class="circle"></div>
                    <h1 class="display-1 font-weight-thin mb-4">{{ presentation.titre }}</h1>
                    <h4 class="subheading">{{ presentation.description }}</h4>
                </v-col>
            </template>

            <template v-if="windowWidth < 600">
                <v-col v-if="presentation" class="container-xs text-center" cols="5">
                    <div class="circle-xs"></div>
                    <h1 class="display-1 font-weight-thin mb-4">{{ presentation.titre }}</h1>
                    <h4 class="subheading">{{ presentation.description }}</h4>
                </v-col>
            </template>
        </v-row>
    </v-parallax>
</template>

<script>
    import axios from "axios";

    export default {
        name: "front_page",
        data: function () {
            return {
                details_file: "files/details.json",
                presentation: null,
                nom: null,
                windowWidth: window.innerWidth,
            }
        },

        methods: {
            getPres: function () {
                const self = this;
                axios.get(this.details_file).then(function (result) {
                    self.presentation = result.data.presentation;
                    self.nom = result.data.name;
                });
            }
        },

        mounted() {
            this.getPres();
            window.onresize = () => {
                this.windowWidth = window.innerWidth
            }
        }

    }

</script>

<style scoped>
    .container {
        position: absolute;
        top: 50%;
        left: 50%;
        right: 100%;
        transform: translate(-50%, -50%);
        width: 80%;
        font-size: 18px;
        padding: 30px;
    }

    .circle {
        width: 300px;
        height: 300px;
        background: url("../files/test.jpg");
        background-size: 100% 100%;
        border-radius: 50%;
        float: left;
        shape-outside: circle();
        margin: 30px;
    }

    .container-sm {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 600px;
        font-size: 15px;
        padding: 15px;
    }

    .circle-sm {
        width: 150px;
        height: 150px;
        background: url("../files/test.jpg");
        background-size: 100% 100%;
        border-radius: 50%;
        float: left;
        shape-outside: circle();
        margin: 15px;
    }

    .container-xs {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 400px;
        font-size: 12px;
        padding: 10px;
    }

    .circle-xs {
        width: 100px;
        height: 100px;
        background: url("../files/test.jpg");
        background-size: 100% 100%;
        border-radius: 50%;
        float: left;
        shape-outside: circle();
        margin: 10px;
    }
</style>